<template>
  <b-row>
    <b-col sm="12">
      <div class="d-flex">
        <h1>Users</h1>
        <b-button variant="success" size="sm" class="ml-auto mb-auto mt-auto" @click="$router.push({name:'Add User'})">Add User</b-button>
      </div>
      <div>
        <b-input-group class="mb-3" style="width:20em">
          <template #append>
            <b-input-group-btn variant="primary" >

              <b-button placeholder="Search by user email"  variant="primary" @click="search()">
                 <CIcon name="cil-search" />
              </b-button>
            </b-input-group-btn>
          </template>
          <b-form-input placeholder="Search" v-model="keyword" v-on:keyup.enter="search()"></b-form-input>
        </b-input-group>
      </div>
      <b-card>
        <b-table  striped hover :items="items.data" :fields="fields" :busy="loading.main">
          <!--loading-->
          <template #table-busy>
            <div class="text-center text-danger ">
              <b-spinner class="align-middle" variant="primary" type="grow"></b-spinner>
            </div>
          </template>

          <template v-slot:cell(name) = row>
            {{`${row.item.first_name} ${row.item.last_name}`}}
          </template>

          <template v-slot:cell(role) = row>
            <span v-if="row.item.role[0]">
              {{row.item.role[0].name}}
            </span>
          </template>

          <template v-slot:cell(location) = row>
            {{`${row.item.country.name}, ${row.item.state.name}`}}
          </template>
          <!--actions-->
          <template v-slot:cell(actions) = row >
              <b-button variant="primary" size="sm"  class="ml-1 mr-1 small-btn" @click="$router.push({name:'User Profile', params:{id:row.item.id}})">Profile</b-button>
              <b-button
                variant="success"
                size="sm"
                class="ml-1 mr-1 small-btn"
                @click="$router.push({name:'Edit User', params:{id:row.item.id}})"
                >Edit</b-button>
              <b-button variant="danger" size="sm" class="ml-1 mr-1 small-btn"  @click="$bvModal.show(`${row.item.id}`)">delete</b-button>
              <b-modal :id="`${row.item.id}`" centered title="Delete" ok-variant="danger" ok-title="Yes" cancel-title="No" @ok="deleteItem(row.item.id)">
                <div>Are you sure that you want to delete this?</div>
              </b-modal>
          </template>

        </b-table>
        <div class="overflow-auto d-flex">
          <b-pagination-nav class="m-auto" @change="getData($route.query.page)" :link-gen="linkGen" :number-of-pages="items.to" use-router></b-pagination-nav>
        </div>
      </b-card>
    </b-col>

  </b-row>
</template>

<script>
import usersData from './UsersData'
import axios from "axios"
import {showToast } from '@/assets/js/index';
export default {
  name: 'Users',
  data () {
    return {
      loading:{
        main:false,
      },
      fields:[
        {key:"name", label:"Name"},

        {key:"email"},

        {key:"location", label: 'Location'},
        { key: "actions", label: "Actions" }
      ],
      items: [],
      keyword:"",

    }
  },
  methods:{
    search() {
      this.loading.main = true;
      if(this.keyword) {
        axios.get(`/admin/v1/users/search/${this.keyword}`).then((res) => {
          this.loading.main = false;
          this.items.data = res.data.data
        })
        .catch((error) => {
          this.loading.main = false;
          console.error(error)
        })
      } else {
        this.getData(this.$route.query.page);
      }
    },
      linkGen(pageNum) {
        return pageNum === 1 ? '?' : `?page=${pageNum}`
      },
    getData(page) {
      this.loading.main = true
      axios.get(`/admin/v1/users?page=${page}`)
      .then((res) => {
        this.loading.main = false;
        console.log(res.data)
        this.items = res.data.data
      })
      .catch((error) => {
        this.loading.main = false;
        console.error(error)
      })
    },
    deleteItem(id){
      axios.delete(`/admin/v1/users/${id}`)
      .then(()=>{
        showToast("success", "Success", "Item deleted successfully!", this)
        //remove deleted item
        this.items.data = this.items.data.filter(item=>{
          return item.id != id
        })
      })
      .catch(err=>{
          this.loading.submit = false;
          for(let error in err.response.data.errors) {
              showToast('danger', `${error}`, `${err.response.data.errors[error][0]}`, this)
          }
          console.log(err.response)
      })
    }
  },
  mounted() {
    this.getData();
  },
  watch: {
    $route: {
      immediate: true,
      handler (route) {
        if (route.query && route.query.page) {
          this.activePage = Number(route.query.page)
        }
      }
    }
  },

}
</script>
<style scoped>
.small-btn {
  width: 4rem!important;
}
</style>